

















import PopUp from "./PopUp.vue";
import { EventBus, EventType } from "@/lib/types/EventBus";
export default {
  name: "EndScore",

  props: {
    score: Number,
    submission: Boolean,
  },

  data() {
    return {
      name: "",
    };
  },

  created() {
    EventBus.$on(EventType.SetInput, (val) => (this.name = val));
  },

  components: { PopUp },
};
